import { useSelector } from "react-redux";
import "./Invite.css"

const Invite = () => {
    const {backgroundColor, textColor, borderColor} = useSelector((state) => state.app.inviteColorStyle);
    
    return(
        <div className="ivite-content" style={{backgroundColor: backgroundColor}}>
            <blockquote style={{color: textColor, borderColor: borderColor}}>
                «Дорогие друзья и родные,
                <br/>
                мы приглашаем Вас на нашу свадьбу!
                <br/><br/>
                Мы будем рады разделить с Вами 
                <br/>
                радость неповторимого
                <br/>
                для нас дня - дня нашей свадьбы!»
            </blockquote>
        </div>
    );
}

export default Invite;